.TrendCard{
	display: flex;
	flex-direction: column;
	gap: 1rem;
	background-color: var(--cardColor);
	padding: 1rem;
	border-radius: 1rem;
}
.trend{
	display: flex;
	flex-direction: column;
	gap: 0.5rem;
}

.trend>span:nth-of-type(1){
	font-weight: 800;
}
.trend>span:nth-of-type(2){
	font-size: 13px;
}